import React ,{useState,useEffect} from 'react';
import './App.css';
import Post from './Post'
import ImageUpload from './ImageUpload';
import { db,auth } from  "./firebase"
import {makeStyles} from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';
import axios from './axios';
import Pusher from 'pusher-js';
function getModalStyle(){
  const top=50;
  const left=50;
  return{
    top:`${top}%`,
    left:`${left}%`,
    transform:`translate(-${top}%,-${left}%)`
  };
}
const useStyles=makeStyles((theme)=>({
  paper:{
    position:'absolute',
    width:400,
    backgroundColor: theme.palette.background.paper,
    border:'2px solid #000',
    boxShadow:theme.shadows[5],
    padding:theme.spacing(2,4,3),
  },
}));
function App() {
  const classes=useStyles();
  const [posts,setPosts]=useState([]);
  const [opensignIn,setOpenSignIn]=useState(false);
  const [open,setOpen]=useState(false);
  const [modalStyle]=useState(getModalStyle);
  const [username,setUsername]=useState('');
  const [password,setPassword]=useState(false);
  const [user,setUser]=useState(null);
  const [email,setEmail]=useState('');
  useEffect(()=>{
    const unsubscribe=auth.onAuthStateChanged((authUser)=>{
      if(authUser){
        //user has logged in
        console.log(authUser);
        setUser(authUser);
      }else{
        //user has logged out
        setUser(null);
      }
    })
    return ()=>{
      unsubscribe();
    }
  },[user,username]);
  const fetchPosts=async ()=>
     await axios.get('/sync').then(response=>{
       console.log(response);
       setPosts(response.data)
  }); 
  useEffect(()=>{
    Pusher.logToConsole = true;
    const pusher = new Pusher('8220ee217a610440154a', {
      cluster: 'eu'
    });
  const channel = pusher.subscribe('posts');
    channel.bind('inserted', (data)=> {
      console.log('data recieved',data);
      fetchPosts();
    });
  },[]);
  useEffect(()=>{ //post is the condition ,that every time post loads
     
       fetchPosts();
  },[]) 
  const signUp=(event)=>{
      event.preventDefault();
      auth.createUserWithEmailAndPassword(email,password)
      .then((authUser)=>{
        return authUser.user.updateProfile({
          displayName:username,

        })
      })
      .catch((error)=>alert(error.message));
  }
  const signIn=(event)=>{
    event.preventDefault();
    auth
    .signInWithEmailAndPassword(email,password)
    .catch((error)=>alert(error.message));
    setOpenSignIn(false);
  }
  // function Post({username,caption,imageUrl}){
  //   const [comments,setComments]=useState([]);
  // }
 
  return (
    <div className="App">
        <Modal
        open={open}
        onClose={()=>setOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <div style={modalStyle} className={classes.paper}>
          <center>
        <img 
        className="app__headerImage"
        src={"https://www.instagram.com/static/images/web/mobile_nav_type_logo.png/735145cfe0a4.png"}
        alt=""
        />
        <form className='app__signup'>
        <Input
          placeholder="name"
          type="text"
          value={username}
          onChange={(e)=>setUsername(e.target.value)}
          />
        <Input
          placeholder="email"
          type="text"
          value={email}
          onChange={(e)=>setEmail(e.target.value)}
          />
          <Input
            placeholder="password"
            type="password"
            value={password}
            onChange={(e)=>setPassword(e.target.value)}
            />
            <Button type="submit" onClick={signUp}>Sign Up</Button>
            </form>
            </center>
        </div>
      </Modal>
      <Modal
        open={opensignIn}
        onClose={()=>setOpenSignIn(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <div style={modalStyle} className={classes.paper}>
          <center>
        <img 
        className="app__headerImage"
        src={"https://www.instagram.com/static/images/web/mobile_nav_type_logo.png/735145cfe0a4.png"}
        alt=""
        />
        <form className='app__signup'>
        <Input
          placeholder="email"
          type="text"
          value={email}
          onChange={(e)=>setEmail(e.target.value)}
          />
          <Input
            placeholder="password"
            type="password"
            value={password}
            onChange={(e)=>setPassword(e.target.value)}
            />
            <Button type="submit" onClick={signIn}>Sign In</Button>
            </form>
            </center>
        </div>
      </Modal>
      <div className="app__header">
        <img 
        className="app__headerImage"
        src={"https://www.instagram.com/static/images/web/mobile_nav_type_logo.png/735145cfe0a4.png"}
        alt=""
        />
        {user?(
        <Button onClick={()=>auth.signOut()}>Logout</Button>
      ):(
        <div className='app__loginContainer'>
          <Button onClick={()=>setOpenSignIn(true)}>Sign In</Button>
          <Button onClick={()=>setOpen(true)}>Sign Up </Button>
        </div>
        
      )}
      </div>
      <div className='app_post'>
      {
        posts.map((post)=>(
          <Post key={post._id} posts={post._id} user={user} username={post.user} caption={post.caption} imageUrl={post.image} />
         ))
      }
      </div>
      {user?.displayName?(
       <ImageUpload username={user.displayName}/>
      ):(
        <h3>Sorry you need to login to upload</h3>
      )}
    </div>
  );
}

export default App;
